import { lazy } from 'react'

const Gallery = [
    {
        path: '/gallery/all',
        component: lazy(() => import('../../pages/gallery/list'))
    },
    {
        path: '/gallery/detail/:id',
        component: lazy(() => import('../../pages/gallery/detail')),
        meta: {
            navLink: '/gallery/detail'
        }
    }
]

export default Gallery
