import { lazy } from 'react'

const Products = [
  {
    path: '/product/all',
    component: lazy(() => import('../../pages/product/list'))
  },
  {
    path: '/product/create',
    component: lazy(() => import('../../pages/product/create'))
  },
  {
    path: '/product/detail/:id',
    component: lazy(() => import('../../pages/product/detail')),
    meta: {
      navLink: '/product/detail'
    }
  },
  {
    path: '/product/:id/product-variant/create',
    component: lazy(() => import('../../pages/product-variant/create')),
    meta: {
      navLink: '/product'
    }
  }
]

export default Products
