import { lazy } from 'react'

const Wardrobes = [
  {
    path: '/wardrobe/all',
    component: lazy(() => import('../../pages/wardrobe/list'))
  },
  {
    path: '/wardrobe/create',
    component: lazy(() => import('../../pages/wardrobe/create')),
    meta: {
      navLink: '/wardrobe/create'
    }
  },
  {
    path: '/wardrobe/detail/:id',
    component: lazy(() => import('../../pages/wardrobe/detail')),
    meta: {
      navLink: '/wardrobe/detail'
    }
  }
]

export default Wardrobes
