import { lazy } from 'react'

const Instagram = [
    {
        path: '/instagram/all',
        component: lazy(() => import('../../pages/instagram/list'))
    },
    {
        path: '/instagram/create',
        component: lazy(() => import('../../pages/instagram/create')),
        meta: {
            navLink: '/instagram/create'
        }
    },
    {
        path: '/instagram/detail/:id',
        component: lazy(() => import('../../pages/instagram/detail')),
        meta: {
            navLink: '/instagram/detail'
        }
    }
]

export default Instagram
